'use strict';
const ERROR = 'error';

const CONFIG = {
	over_withdrawal_amount: {
		type: ERROR,
		en: " The single minimum withdrawal amount is 5,000 BxC, and the maximum withdrawal amount is 1,000,000 BxC.",
		zn: " 5,000 BxC 1,000,000 BxC"
	},
	RELOAD_THE_PAGE: {
		type: ERROR,
		zn: '',
		en: 'reload the page'
	},
	amount_error: {
		type: ERROR,
		zn: '',
		en: 'amount error'
	},
	password_error: {
		type: ERROR,
		zn: '',
		en: 'password error'
	},
	limit_email_code: {
		type: ERROR,
		zn: '',
		en: 'limit email code'
	},
};

import { Message } from 'element-ui'


let messageTips = (type, locals) => {
	let tips = CONFIG[type] || CONFIG.RELOAD_THE_PAGE
	tips.message = tips[locals] || tips.en;

	Message(tips);
};

export {
	messageTips
};


