<template>
  <div class="home">
    <Header type='login-header' />
    <AccountLayout>
      <!--  -->
      <div class="account-dialog">
        <div class="title">{{ $t('register.registerTitle') }}</div>
        <!--  -->
        <BasicInput
          type="text"
          icon-type="email"
          class="account-input account-email"
          :placeValue="$t('register.registerPlaceHolder')"
          v-model="inputEmail"
        ></BasicInput>
        <div
          v-if="EmailErrMsg"
          class="account-error"
        >
          <i class="el-alert__icon el-icon-error"></i>
          <span class="EmailErrorMsg">{{ EmailErrMsg }}</span>
        </div>
        <!-- & -->
        <EmailCodeByGVerify :siteKey="siteKey" type="text" icon-type="emailCode" class="account-input password-email" v-model="inputEmailCode" :email="inputEmail" @emailCodeTip="emailCodeTip" :placeValue="$t('register.verfPlaceHolder')"></EmailCodeByGVerify>
        <div v-if="EmailCodeErrMsg" class="account-error">
          <i class="el-alert__icon el-icon-error"></i>
          <span class="EmailCodeErrMsg">{{ EmailCodeErrMsg }}</span>
        </div>
        <!--  -->
        <BasicInput
          type="password"
          icon-type="password"
          class="account-input password-email"
          v-model="inputPw"
          :placeValue="$t('register.newPw')"
        ></BasicInput>
        <div
          v-if="PwErrMsg"
          class="account-error"
        >
          <i class="el-alert__icon el-icon-error"></i>
          <span class="PwErrMsg">{{ PwErrMsg }}</span>
        </div>
        <!--  -->
        <BasicInput
          type="password"
          icon-type="password"
          class="account-input password-email"
          v-model="inputSePw"
          :placeValue="$t('register.confirmPw')"
        ></BasicInput>
        <div
          v-if="secPsErrMsg"
          class="account-error"
        >
          <i class="el-alert__icon el-icon-error"></i>
          <span class="secPsErrMsg">{{ secPsErrMsg }}</span>
        </div>
        <!-- agree protocol -->
        <div class="protocol-wrap">
          <el-radio
            v-model="agreeProtocol"
            label="1"
          >
            {{$t('register.agreeProtocol')}}
          </el-radio>
          <router-link
            class="agree-link"
            target="_blank"
            to="/protocol"
          > {{$t('register.agreeLink')}} </router-link>
        </div>
        <div
          class="sign-up-button bonus-cursor"
          v-on:click="signUp"
        >{{ $t('register.signButton') }}</div>
        <div
          class="login-sign-wrap bonus-cursor"
          v-on:click="login"
        >{{ $t('register.redirectLogin') }}</div>
      </div>
    </AccountLayout>
  </div>
</template>

<script>
// @ is an alias to /src
import Header from "@/components/Header.vue";
import AccountLayout from "@/components/AccountLayout.vue";
import BasicInput from "@/components/BasicInput.vue";
import EmailCodeByGVerify from "@/components/Common/EmailCodeByGVerify.vue";
import { mapMutations, mapActions, mapState } from "vuex";
import { Message } from "element-ui";
import { SITEKEY } from '../config/contant.js'

export default {
  name: "SignUp",
  data() {
    return {
      //
      EmailErrMsg: "",
      ImageCodeErrMsg: "",
      EmailCodeErrMsg: "",
      PwErrMsg: "",
      secPsErrMsg: "",
      // 
      inputEmail: "",
      inputEmailCode: "",
      inputPw: "",
      inputSePw: "",
      //
      isSignUpDisable: false,
      agreeProtocol: false
    };
  },
  components: {
    Header,
    AccountLayout,
    BasicInput,
    EmailCodeByGVerify
  },
  computed: mapState({
    // 
    imageCodeSrc: state => state.signUp.imageCodeSrc,
    siteKey() {
      return SITEKEY.LOW
    }
  }),
  methods: {
    ...mapActions(["ajaxSignUp"]),
    // 
    login() {
      this.$router.push({ name: "login" });
    },
    // 
    signUp() {
      const {
        inputEmail,
        inputEmailCode,
        inputPw,
        inputSePw,
        isSignUpDisable
      } = this;

      // 
      if (isSignUpDisable) {
        return true;
      }

      // -----
      const emailRule = /^([\.a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,6}){1,2})$/;
      if (emailRule.test(inputEmail)) {
        this.EmailErrMsg = "";
      } else {
        this.isSignUpDisable = false;
        this.EmailErrMsg = "The email error";
        return true;
      }

      // 
      if (inputEmailCode) {
        this.EmailCodeErrMsg = "";
      } else {
        this.isSignUpDisable = false;
        this.EmailCodeErrMsg = "The Cerification Code error";
        return true;
      }

      if (inputPw && inputPw.length >= 6) {
        this.PwErrMsg = "";
      } else {
        this.isSignUpDisable = false;
        this.PwErrMsg = "The password error";
        inputPw.length < 6 &&
          (this.PwErrMsg = "The password length should more then 6");
        return true;
      }

      if (inputSePw && inputSePw === inputPw) {
        this.secPsErrMsg = "";
      } else {
        this.isSignUpDisable = false;
        this.secPsErrMsg =
          inputSePw === inputPw
            ? "The confirm password error"
            : "The confirm password error";
        return true;
      }

      if (!this.agreeProtocol) {
        Message({
          message: "you have not agree to the User Agreement",
          type: "error"
        });
        return true;
      }

      this.ajaxSignUp({
        email: inputEmail,
        emailVerifyCode: inputEmailCode,
        password: inputPw,
        rePassword: inputSePw,
        refer: this.$route.query.refer || ""
      }).then(res => {
        this.isSignUpDisable = false;
        if (res && res.code && res.code === 201) {
          Message({
            message: "Register success, redirect after 3s",
            type: "success"
          });
          setTimeout(() => {
            this.$router.push({ name: "login" });
          }, 3000);
        } else {
          Message(res.message || "network error");
        }
      });
    },
    // 
    emailCodeTip(error) {
      console.log(error);
      if (error.type === "captcha") {
        this.EmailCodeErrMsg = error.message;
      } else if (error.type === "email") {
        this.EmailErrMsg = error.message;
      }
    }
  }
};
</script>


